import { Searcher } from "../components/modules/Searcher";

let initalState = {
  searcherMode: "COMPACT", // Switch compact or fullsize mode (top-righg corner)
  searcherValue: "",
  searchItems: [".tp-title", ".tp-text", ".tp-sublink", ".tp-code pre"],

  search(expression, elements) {
    return new Searcher(expression, elements).search();
  },
};

export const searchReducer = (state = initalState, action) => {
  switch (action.type) {
    case "SWITCH_SEARCHER_MODE": {
      // Searcher fullsize/compact mode switch action api
      state.searcherMode = action.payload;
      break;
    }

    case "CHANGE_SEARCHER_VALUE": {
      // Change value of the header searcher line (input)
      state.searcherValue = action.payload;
      break;
    }

    case "SEARCH_EXPRESSION": {
      // Search an expression in some html items
      action.payload.search(action.payload.expression, action.payload.items);
      break;
    }
  }

  return state;
};
